import styled from "@emotion/styled";
import { ButtonNoPadding, Row } from "components/lib";
import { useAuth } from "context/auth-context";
// import React from "react";
import { Routes, Route, Navigate } from "react-router";
// react-router 和 react-router-dom 的关系相当于react 与react-native
// import { BrowserRouter as Router } from "react-router-dom";
import { ProjectListScreen } from "screens/project-list";
// 使用 svg 的格式，可以更改样式
import { ReactComponent as SoftwareLogo } from "assets/software-logo.svg";
import { Button, Dropdown, Menu } from "antd";
import { ProjectScreen } from "screens/project";
import { ProjectModal } from "screens/project-list/project-modal";
import { ProjectPopover } from "components/project-popover";
import { resetRoutes } from "utils";
import { UserPopover } from "components/user-popover";

/**
 * grid 和 flex 各自的应用场景
 * 1. 要考虑，是一维布局 还是 二维布局
 * 一般来说，一维布局用flex，二维布局用grid
 * 2. 是从内容出发还是从布局出发？
 * 从内容出发：你先有一组内容(数量一般不固定),然后希望他们均匀的分布在容器中
 * 由内容自己的大小决定占据的空间
 * 从布局出发：先规划网格(数量一般比较固定)，然后再把元素往里填充
 * 从内容出发，用flex
 * 从布局出发，用grid
 *
 */

export default function AuthenticatedApp() {
  // const [projectModalOpen, setProjectModalOpen] = useState(false);

  return (
    <Container>
      <HeaderPage />
      <Main>
        <Routes>
          <Route path={"/projects"} element={<ProjectListScreen />} />
          <Route path={"/projects/:projectId/*"} element={<ProjectScreen />} />
          <Navigate to={"/projects"} />
          {/* <Route index element={<ProjectListScreen />} /> */}
        </Routes>
      </Main>
      <ProjectModal />
    </Container>
  );
}

const HeaderPage = () => {
  return (
    <Header between={true}>
      <HeaderLeft gap={true}>
        {/* <img src={logo} alt="" /> */}
        <ButtonNoPadding type={"link"} onClick={resetRoutes}>
          <SoftwareLogo width={"18rem"} color={"rgb(38,132,255)"} />
        </ButtonNoPadding>
        <ProjectPopover />
        <UserPopover />
      </HeaderLeft>
      <HeaderRight>
        <User />
      </HeaderRight>
    </Header>
  );
};
// AuthenticatedApp.whyDidYouRender = true;

const User = () => {
  const { logout, user } = useAuth();

  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={"logout"}>
            <Button type={"link"} onClick={logout}>
              登出
            </Button>
          </Menu.Item>
        </Menu>
      }
    >
      <Button type={"link"} onClick={(e) => e.preventDefault()}>
        Hi,{user?.name}
      </Button>
    </Dropdown>
  );
};

// temporal dead zone(暂时性死区)
const Container = styled.div`
  display: grid;
  grid-template-rows: 7rem 1fr;
  height: 100vh;
`;

// const PageHeader = styled.header`
//   height: 6rem;
// `;
const Main = styled.main`
  display: flex;
  overflow: hidden;
`;
const Header = styled(Row)`
  padding: 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  z-index: 1;
`;
const HeaderLeft = styled(Row)`
  font-weight: 530;
  /* font-size: px; */
`;

const HeaderRight = styled.div``;
