/** @jsxImportSource @emotion/react */
import { Button, List, Modal } from "antd";
import { Row, ScreenContainer } from "components/lib";
import dayjs from "dayjs";
import { useState } from "react";
import { Link } from "react-router-dom";
import { Epic } from "type/epic";
import { Project } from "type/project";
import { Task } from "type/task";
import { useDeleteEpic, useEpics } from "utils/epic";
import { useTasks } from "utils/task";
import { useProjectInUrl } from "../kanban/utils";
import { CreateEpic } from "./create-epic";
import { useEpicSearchParams, useEpicsQueryKey } from "./utils";

export const EpicScreen = () => {
  const { data: currentProject } = useProjectInUrl();
  const { data: epics } = useEpics(useEpicSearchParams());
  const { data: tasks } = useTasks({ projectId: currentProject?.id });
  const { mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey());
  const [epicCreateOpen, setEpicCreateOpen] = useState(false);

  const startDelete = ({ epic }: { epic: Epic }) => {
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除任务组吗？",
      onOk() {
        deleteEpic({ id: epic.id });
      },
    });
  };
  return (
    <ScreenContainer>
      <Row between={true}>
        <h1>{currentProject?.name}任务组</h1>
        <Button onClick={() => setEpicCreateOpen(true)}>创建任务组</Button>
      </Row>
      <ListRender
        epics={epics}
        tasks={tasks}
        currentProject={currentProject}
        startDelete={startDelete}
      />
      <CreateEpic
        onClose={() => setEpicCreateOpen(false)}
        visible={epicCreateOpen}
      />
    </ScreenContainer>
  );
};

interface ListProps {
  epics: Epic[] | undefined;
  tasks: Task[] | undefined;
  currentProject: Project | undefined;
  startDelete: ({ epic }: { epic: Epic }) => void;
}

const ListRender = ({
  epics,
  tasks,
  currentProject,
  startDelete,
}: ListProps) => {
  return (
    <List
      css={{
        overflow: "scroll",
        "::-webkit-scrollbar": {
          /*隐藏滚轮*/
          display: "none",
        },
      }}
      dataSource={epics}
      itemLayout={"vertical"}
      renderItem={(epic) => (
        <List.Item>
          <List.Item.Meta
            title={
              <Row between={true}>
                <span>{epic.name}</span>
                <Button type={"link"} onClick={() => startDelete({ epic })}>
                  删除
                </Button>
              </Row>
            }
            description={
              <div>
                <div>开始时间：{dayjs(epic.start).format("YYYY-MM-DD")}</div>
                <div>结束时间：{dayjs(epic.end).format("YYYY-MM-DD")}</div>
              </div>
            }
          />
          <div>
            {tasks
              ?.filter((task) => task.epicId === epic.id)
              .map((task) => (
                <div>
                  <Link
                    to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                    key={task.id}
                  >
                    {task.name}
                  </Link>
                </div>
              ))}
          </div>
        </List.Item>
      )}
    />
  );
};
